
import Password from '@/components/password/password.vue'
import BlocInvisible from '@/components/blocInvisible/blocInvisible.vue'

import common from '@/common/functions.js'

export default {
    name: "ChambreIndex",
    components: {
        BlocInvisible,
        Password
    },
    data() {
        return {
            urlImage: require("@/assets/images/chambre.png"),
            song: new Audio(require('@/assets/musics/halloween_slow.mp3')),
            showDame: false,
            nbIndiceTrouve: 0,
            displayEndButton: false,
            coords: []
        }
    },
    created() {
        this.song.play();
    },

    methods: {
        incrementIndice() {
            this.nbIndiceTrouve += 1;
            if (this.nbIndiceTrouve == 7) {
                this.displayEndButton = true;
            }
        },

        goToFirstView() {
            common.cacherElements();


            let btnFin = document.getElementById("btn-fin");
            btnFin.style.animation = "disparition 3s forwards";

            let background = document.getElementById("mainChambre");
            background.classList.add("zoomIn")

            // 12 sec
            setTimeout(() =>
                // chargement des params url
                this.$router.push('/?end=oui')
                , 12000);

            // La musique
            this.decreaseSong();
        },

        decreaseSong() {
            let timer = setInterval(() => {
                if (this.song.volume - 0.1 < 0) {
                    clearInterval(timer);
                } else {
                    this.song.volume -= 0.1;
                }
            }, 1000)
        },



        clickImg(e) {
            var cursorLeft = e.layerX;
            var cursorTop = e.layerY;

            var heightBg = e.target.height;
            var widthBg = e.target.width;

            this.coords.push(
                {
                    left: cursorLeft,
                    top: cursorTop
                }
            );

            if (this.coords.length == 2) {

                let hauteur = (this.coords[1].top - this.coords[0].top) * 100 / heightBg;
                let largeur = (this.coords[1].left - this.coords[0].left) * 100 / widthBg;

                let left = this.coords[0].left * 100 / widthBg;
                let top = this.coords[0].top * 100 / heightBg;


                console.log(`hauteur: ${hauteur.toFixed(2)}\nlargeur: ${largeur.toFixed(2)}\nleft: ${left.toFixed(2)}\ntop: ${top.toFixed(2)}`)
                this.coords = [];
            }
        },
        

        resizeImage() {
            var image = document.getElementById("imgChambre");
    
            if (image.offsetWidth > window.innerWidth) {
                image.style.width = "100%";
                image.style.height = "auto";
            
                var container = image.parentElement;
                container.style.width = "100%";        
                container.style.height = "max-content"; 
            }       
        }
    }
}